import React from 'react'

import { useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'

import { CircularProgress, Card, CardMedia, CardContent, CardActions, CardActionArea, Button, LinearProgress } from '@material-ui/core'
import config from '../config'

import { Contract } from '@ethersproject/contracts'
import ETHERIUM from '../services/etherium'
import SELLERV1 from '../services/etherium/contracts/sellerv1'
import ETH from '../components/eth'
import * as SaltSeason from '../models/salt_season'
import { BigNumber } from '@ethersproject/bignumber'

type Props = {
  saltSeason: SaltSeason.SaltSeason
}

function SaltSeasonComponent(props: Props) {
  const { saltSeason } = props
  const provider = ETHERIUM.getJsonRpcProvider(config.INFURA_JSON_RPC_API[saltSeason.networkId], saltSeason.networkId);

  const context = useWeb3React<Web3Provider>()
  const { account, library } = context

  const [contract, setContract] = React.useState<Contract | undefined>(undefined)
  React.useEffect(() => {
    if (contract !== undefined) { return }
    const _contract = SELLERV1.getContract(saltSeason.sellerContractAddress, provider)
    setContract(_contract)
  }, [contract])

  const [currentPrice, setCurrentPrice] = React.useState<BigNumber | undefined>(undefined)
  React.useEffect(() => {
    if (!!currentPrice) { return } // guard: already done
    if (!contract) { return } // guard

    (async () => {
      const currentPrice = await contract.currentPrice()
      setCurrentPrice(currentPrice)
    })()
  }, [currentPrice, contract])

  const [priceStep, setPriceStep] = React.useState<BigNumber | undefined>(undefined)
  React.useEffect(() => {
    if (!!priceStep) { return } // guard: already done
    if (!contract) { return } // guard

    (async () => {
      const priceStep = await contract.priceStep()
      setPriceStep(priceStep)
    })()
  }, [priceStep, contract])

  const [tokenPurchaseEvents, setTokenPurchaseEvents] = React.useState<Event[] | undefined>(undefined)
  React.useEffect(() => {
    if (!contract) { return }
    if (tokenPurchaseEvents !== undefined) { return }
    (contract as any).queryFilter(contract.filters.TokenPurchase).then((events: Event[]) => {
      // console.log(events)
      setTokenPurchaseEvents(events.filter(e => (e as any).event === "TokenPurchase"))
    })
  }, [contract, tokenPurchaseEvents])

  return (
    <Card style={{margin: '2em', display: 'inline-block'}}>
      <CardActionArea>
        <a href="https://marketplace.saltandsatoshi.com/#" style={{textDecoration: 'none'}}>
          <CardMedia
            image={saltSeason.posterUrls[0]}
            style={{height: 300, minWidth: 300}}
          />
        </a>
        <CardContent>
          <a href="https://marketplace.saltandsatoshi.com/#" style={{textDecoration: 'none'}}>
            <p style={{ fontSize: '2em' }} className="with-cool-font">Season {saltSeason.number}</p>
            <br/>
            <h5 className="with-cool-font">{saltSeason.title}</h5>
          </a>
        </CardContent>
      </CardActionArea>
      <CardActions style={{padding: 0}}>
      
        <div style={{display: 'flex', flexDirection: 'column', width: '100%'}}>
          { !!tokenPurchaseEvents ? <LinearProgress variant='determinate' color="secondary" value={100*Math.min(tokenPurchaseEvents.length/saltSeason.tokenIds.length, 1.0)}/> : null }
          <div style={{display: 'flex', backgroundColor: 'orange'}}>
            <Button variant="contained" color="secondary" style={{flex: 1, borderRadius: 0, backgroundColor: '#d33747'}}>
              <div style={{display: 'flex', flexDirection: 'column'}} className="with-cool-font">
                <span style={{fontSize: 'x-small'}}>Sold</span>
                <span style={{fontSize: 'xx-large'}}>
                  { !!tokenPurchaseEvents ? <>{tokenPurchaseEvents.length}</> : null }
                </span>
              </div>
            </Button>
            <Button variant="contained" color="secondary" style={{flex: 1, borderRadius: 0, backgroundColor: '#d33747'}}>
              <div style={{display: 'flex', flexDirection: 'column'}} className="with-cool-font">
                <span style={{fontSize: 'x-small'}}>Edition of</span>
                <span style={{fontSize: 'xx-large'}}>{saltSeason.tokenIds.length}</span>
              </div>
            </Button>
            <Button variant="contained" color="primary" style={{flex: 1, borderRadius: 0, backgroundColor: '#d33747'}}>
              <div style={{display: 'flex', flexDirection: 'column'}} className="with-cool-font">
                <span style={{fontSize: 'x-small'}}>Current price</span>
                <span style={{fontSize: 'x-large'}}>
                  {!currentPrice ? 
                    <CircularProgress size={15}/> :
                    <ETH.Number number={currentPrice}/>}
                </span>
              </div>
            </Button>
          </div>
        </div>
        
      </CardActions>
    </Card>)
}

export default SaltSeasonComponent