import React from 'react'

import Loading from '../components/loading'

import * as SaltSeason from '../models/salt_season'
import SaltSeasonComponent from './_salt_season'

import { Box } from '@material-ui/core'

function Reducer() {
  const chainId = 42
  const [saltSeasons, setSaltSeasons] = React.useState<SaltSeason.SaltSeason[] | undefined>(undefined)
  React.useEffect(() => { // Load auctions from Firebase
    if (!!saltSeasons) { return } // guard: already done
    // if (!chainId) { return }

    (async () => {
      const auctions = await SaltSeason.indexByNetworkId(chainId || 42)
      setSaltSeasons(auctions)
    })()
  }, [saltSeasons, chainId])

  if (!saltSeasons) { return <Loading/> }

  return (
    <div>
      <section id="redemption-section">
        <div id="container19" className="container default full screen" style={{minHeight: 'auto'}}>
          <div className="wrapper">
            <div className="inner" style={{padding: '3em 3em 1em 3em'}}>
              <h2 id="text33" style={{ fontWeight: 'normal', textTransform: 'none', color: '#fff' }} className="with-cool-font">Showcase</h2>
            </div>
          </div>
        </div>
      </section>

      <Box style={{marginTop: '3em', color: 'lightgray', float: 'left'}}>
        {
          saltSeasons.map(saltSeason => (
            <SaltSeasonComponent saltSeason={saltSeason}/>
          ))
        }
      </Box>
    </div>)
}

export default Reducer